.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  position: relative;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn,
.btn:link,
.btn:visited {
  text-decoration: none;
  width: 8rem;
  display: inline-block;
  border-radius: 1rem;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  position: relative;
  font-size: 1.6rem;
  border: none;
  cursor: pointer;
}

.btn:hover {
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
  -webkit-box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}

.btn:hover::after {
  -webkit-transform: scaleX(1.2) scaleY(1.4);
  transform: scaleX(1.2) scaleY(1.4);
  opacity: 0;
}

.btn:hover .btn--fetch--icon {
  fill: #3ab899e1;
}

.btn:hover .btn--exit--icon,
.btn:hover .btn--logout--icon {
  fill: #fd5a5a;
}

.btn:hover .btn--save--icon {
  fill: #dbc85a;
}

.btn:hover .btn--restore--icon {
  fill: #65db5a;
}

.btn:active,
.btn:focus {
  outline: none;
  -webkit-transform: translateY(-1px);
  transform: translateY(-1px);
  -webkit-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.btn--white {
  background-color: #fff;
  color: #777;
}

.btn--white::after {
  background-color: #fff;
}

.btn svg {
  width: 3rem;
  height: 3rem;
  fill: #a1a1a1;
}

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}

.btn--animated {
  -webkit-animation: moveInBottom 0.5s ease-out 0.75s;
  animation: moveInBottom 0.5s ease-out 0.75s;
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}

.btn--sicon svg {
  width: 3.8rem;
  height: 3.8rem;
}

.btn--green {
  background-color: #3ab899e1;
  color: #fff;
}

.btn--green::after {
  background-color: #3ab899e1;
}

.btn--black {
  background-color: #111111;
  color: #fff;
}

.btn--black::after {
  background-color: #111111;
}

.btn--red {
  background-color: #d24d42;
  color: #fff;
}

.btn--red::after {
  background-color: #d24d42;
}

.btn--exit {
  background: #111111;
}

.btn--exit--icon {
  -webkit-transition: all 1s;
  transition: all 1s;
}

.btn--exit svg {
  width: 3.8rem;
  height: 3.8rem;
  margin-left: 11px;
  margin-top: 4px;
}

.btn--fetch {
  background-color: #111111;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.btn--fetch--icon {
  -webkit-transition: all 1s;
  transition: all 1s;
}

.btn--fetch::after {
  background-color: #111111;
}

.btn--save {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  background-color: #111111;
  width: 5rem;
  height: 5rem;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.btn--save--icon {
  -webkit-transition: all 1s;
  transition: all 1s;
}

.btn--save svg {
  width: 3rem;
  height: 3rem;
}

.btn--save::after {
  background-color: #111111;
}

.btn--logout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  color: #a1a1a1;
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  background-color: #111111;
  width: 10rem;
  height: 5rem;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.btn--logout--icon {
  -webkit-transition: all 1s;
  transition: all 1s;
}

.btn--logout svg {
  width: 3rem;
  height: 3rem;
  fill: #a1a1a1;
}

.btn--logout::after {
  background-color: #111111;
}

.btn--restore {
  position: absolute;
  bottom: 2rem;
  right: 8rem;
  background-color: #111111;
  width: 5rem;
  height: 5rem;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.btn--restore--icon {
  -webkit-transition: all 1s;
  transition: all 1s;
}

.btn--restore svg {
  width: 3.8rem;
  height: 3.8rem;
}

.btn--restore::after {
  background-color: #111111;
}

.btn-text:link,
.btn-text:visited {
  font-size: 1.6rem;
  color: #242c1f;
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #242c1f;
  padding: 3px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.btn-text:hover {
  background-color: #242c1f;
  color: #fff;
  -webkit-box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
}

.btn-text:active {
  -webkit-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.title {
  position: absolute;
  top: 2rem;
}

.container {
  display: flex;
  justify-content: space-evenly;
  width: 80%;
  height: 50%;
  gap: 2rem;

  margin-bottom: 1rem;
}

.card {
  position: relative;
  background-color: #111111a9;
  width: 14rem;
  height: 8rem;
  border-radius: 10px;
  transition: all 0.5s;
  font-size: 2.5rem;

  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card:hover {
  transform: scale(1.08) translate3d(0, 0, 0) perspective(1px);
  background-color: #17758f;
}

#notify {
  position: absolute;
  bottom: 1.5rem;
}
